import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faTrashCan} from "@fortawesome/free-regular-svg-icons";

function Product({product, onRemove}) {
  return (
    <>
        <li>
          <p>{product.id}</p>
          <img src={`${process.env.PUBLIC_URL}/images/${product.thumb}`} alt={product.title} />
          <p>제목 : {product.title}</p>
          <p>감독 : {product.director}</p>
          <p>개봉일 : {product.release}</p>
          <p><span onClick={()=>onRemove(product.id)}><FontAwesomeIcon icon={faTrashCan} className="trash"/></span>
          </p>
        </li>
    </>
  );
}

export default Product;