import React from 'react';

function Create({thumb, title, director, release, onDataChange, onCreate}) {
  return (
    <>
      <div className='vibe'>
        <h2>상품 데이터 입력/출력 삭제하기</h2>
        <dl>
          <dt>포스터</dt>
          <dd><input type='text' name="thumb" placeholder='이미지 파일명' value={thumb} onChange={onDataChange} /></dd>
          <dt>제목</dt>
          <dd><input type='text' name="title" placeholder='제목' value={title} onChange={onDataChange}  /></dd>
          <dt>감독</dt>
          <dd><input type='text' name="director" placeholder='감독' value={director} onChange={onDataChange}  /></dd>
          <dt>개봉일</dt>
          <dd><input type='text' name="release" placeholder='개봉일' value={release} onChange={onDataChange}  /></dd>
          <button type='submit' onClick={onCreate}>등록</button>
        </dl>
      </div>
    </>
  );
}

export default Create;