import React, {useRef, useState} from 'react';
import Create from './Create';
import Product from './Product';
import dataProducts from '../data/data.json';
import {Swiper, SwiperSlide} from 'swiper/react';
import {Navigation, Scrollbar, Autoplay} from 'swiper';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import 'swiper/css/autoplay';

function ProductList() {
  const [products, setProducts] = useState(dataProducts.keys);
  
  const nextId = useRef(6);

  // 입력상자에서 사용할 값을 state로 관리한다.
  const [inputs, setInputs] = useState({
    thumb: '',
    title: '',
    director: '',
    release: ''
  });

  // 비구조화 할당 방식으로 state값 각각 변수에 담아주기
  const {thumb, title, director, release} = inputs;

  // onDataChange함수 작성하기
  const onDataChange=(e)=>{
    const{name, value}=e.target;
     // state값 변경
    setInputs({
      ...inputs, // 변경되는 것 외에 나머지 속성값을 의미하는 나머지 패턴임
      [name]:value // 내부에서 밖의 변수를 속성명으로 사용시 [변수명]
    });
  }

  // 추가버튼 클릭시 기존배열 뒤에 새로운 배열데이터를 추가하는 함수
  const onCreate=()=>{
    const product={
      id: 'No.'+nextId.current,
      thumb,
      title,
      director,
      release
    }
    setProducts([...products, product]);
    setInputs({
      thumb: '',
      title: '',
      director: '',
      release: ''
    });
    nextId.current+=1;
  }
  //배열 데이터 삭제하기
  const onRemove = id =>{
    setProducts(products.filter(product=> product.id!==id))
  };
  return (
    <>
      <Swiper
        modules={[Navigation, Scrollbar, Autoplay]} className="mySwiper"

        spaceBetween={50}
        centeredSlides={true}
        autoplay={{
          delay:3000,  
          disableOnInteraction: false,
        }}
        navigation={true}
        slidesPerView={1}

        loop={true}

        scrollbar={{draggable:true}}
        >
      
        <SwiperSlide><span className='text_plus'>[기타]	개인정보처리방침 개정 안내 (1/16 字 시행)</span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[시스템점검]	2023년 2월 시스템 점검 안내</span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[극장]	[CGV대구연경] 휴점일 공지 (4월 3일)</span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[극장] [CGV대구한일] 4/2(일) &#x003C;2023 대구국제마라톤대회&#x003E; 개최에 따른 교통통제 사전안내 件</span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[행사/이벤트] 아트하우스 클럽&#x003C;본즈 앤 올&#x003E; 마그넷 철지(배경지) 교환 안내</span></SwiperSlide>
      </Swiper>

      <Create onDataChange={onDataChange} thumb={thumb} title={title} director={director} release={release} onCreate={onCreate} />
      <ul className='movie_list'>
        {products.map((product=><Product product={product} key={product.id} onRemove={onRemove}/>))}
      </ul>
    </>
  );
}

export default ProductList;